<template>
	<view>
		<view class="login" v-if="!hasLogin">
			<text>hi ÁÇπÂáªÁôªÂΩïÊåâÈíÆ‰ª£Ë°®‰Ω†ÂêåÊÑèÊàë‰ª¨ÁöÑ‚ÄúÁî®Êà∑ËßÑËåÉ‚ÄùÂíå‚Äú‰ΩøÁî®È°ªÁü•‚Äù ‰∏ãÊ¨°ËøõÂÖ•Â∞èÁ®ãÂ∫èÂ∞ÜËá™Âä®ÁôªÂΩï peace~</text>
			<button open-type="getUserInfo" @getuserinfo="getUserInfo">ÁôªÈôÜ</button>
		</view>
		<userCenter :userInfo="userInfo" type="mine" v-on:editMotto="handleMotto">
			<template #coverSlot>
				<view @tap="uploadCover">
					<view v-if="!userInfo.coverUrl" class="coverTip">üëÄËøòÊ≤°ÊúâÂ∞ÅÈù¢ÔΩûÁÇπÊàë</view>
					<image v-else :src="userInfo.coverUrl" mode="widthFix"></image>
				</view>
			</template>
			<template #editor>
				<navigator url="../editor/editor">
					<view class="editInfo">
						<uni-icons type="gear-filled" size="17" color="skyblue"></uni-icons>
						<text>ÁºñËæë</text>
					</view>
				</navigator>
			</template>
		</userCenter>
	</view>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
const db = wx.cloud.database();
export default {
	data() {
		return {};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		...mapMutations(['login', 'update', 'commitMsg']),
		getUserInfo(e) {
			uni.showLoading({
				mask: true,
				title: 'ÁôªÂΩï‰∏≠'
			});
			console.log(e.detail.userInfo);
			let userInfo = e.detail.userInfo;
			this.databaseInit(userInfo);
		},
		async databaseInit(data) {
			await wx.cloud
				.callFunction({
					name: 'initUser',
					data: data
				})
				.then(async res => {
					//res.result.id
					console.log(res);
					await wx.cloud
						.callFunction({
							name: 'sqlData',
							data: {
								name: 'users',
								_id: res.result._id
							}
						})
						.then(async res => {
							this.login(res.result.data);
							// !!!
							// TODO
							// this.watchMsg();
							wx.setNavigationBarTitle({
								title: res.result.data.nickName + 'ÁöÑ‰∏ªÈ°µ'
							});
							uni.hideLoading();
						});
				});
		},
		uploadCover() {
			const openid = this.userInfo._openid;
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'], //ÂèØ‰ª•ÊåáÂÆöÊòØÂéüÂõæËøòÊòØÂéãÁº©ÂõæÔºåÈªòËÆ§‰∫åËÄÖÈÉΩÊúâ
				sourceType: ['album'] //‰ªéÁõ∏ÂÜåÈÄâÊã©
			})
				.then(res => {
					let tempPath = res[1].tempFiles[0].path;
					// ÂéãÁº©
					uni.compressImage({
						src: tempPath,
						quality: 1
					}).then(res => {
						tempPath = res[1].tempFilePath;
						// 
						let time = Date.now()
						time = String(time)
						time = time.substring(10)
						wx.cloud
							.uploadFile({
								cloudPath: `userCover/${openid}_cover_${time}.jpg`,
								filePath: tempPath // Êñá‰ª∂Ë∑ØÂæÑ
							})
							.then(async res => {
								console.log(res.fileID);
								console.log(res)
								if (res.errMsg === "cloud.uploadFile:ok") {
									await wx.cloud
										.callFunction({
											name: 'update',
											data: {
												name: 'users',
												_openid: this.userInfo._openid,
												updates: {
													coverUrl: res.fileID
												}
											}
										})
										.then(res => {
											console.log(res);
											uni.hideLoading();
										});
								}
							});
					});
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
				});
		},
		handleMotto(data) {
			console.log('Âà∑Êñ∞Á≠æÂêç');
			console.log(data);
			wx.cloud
				.callFunction({
					name: 'update',
					data: {
						name: 'users',
						_openid: this.userInfo._openid,
						updates: {
							motto: data
						}
					}
				})
				.then(res => {
					console.log(res);
					// this.update({
					// 	motto:data
					// })
				})
				.catch(err => {
					uni.showToast({
						title: 'ËØ∑Á≠â‰ºöÂÜçËØïËØïÂêß' + err,
						icon: 'none'
					});
				});
		},
	},
	  onShareAppMessage: function () {
	    return {
	      title: 'Âø´Êù•ÊâæÊàëÁé©ËÄç',
	      path: '/index/index?id=123'
	    }
	  },
};
</script>

<style>
.coverTip {
	line-height: 420rpx;
	text-align: center;
	font-weight: bolder;
}
.editInfo {
	color: skyblue;
	border: 1rpx solid skyblue;
	border-radius: 8rpx;
	padding: 8rpx;
	margin-right: 20rpx;
	background-color: #232323;
}
.login {
	position: fixed;
	width: 400rpx;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	margin: 0 auto;
	z-index: 1;
	color: #f8f8f8;
}
</style>
